import React, { useEffect, useState } from "react";
import symbol_summoners_rift from "../../../../../images/icons/summoners_rift.png";
import symbol_aram from "../../../../../images/icons/aram.png";

export enum mode {
  SUMMONERS_RIFT,
  ARAM,
}

interface props {
  type: mode;
  win?: number;
  lose?: number;
}

function GameRate(info: props) {
  const [title, setTitle] = useState("");
  const [symbol, setSymbol] = useState("");
  const [rate, setRate] = useState(0);

  useEffect(() => {
    switch (info.type) {
      case 0:
        setTitle("소환사의 협곡");
        setSymbol(symbol_summoners_rift);
        break;
      case 1:
        setTitle("칼바람 나락");
        setSymbol(symbol_aram);
        break;
    }

    setRate(
      Math.round(((info.win || 0) / ((info.win || 0) + (info.lose || 0))) * 100)
    );
  }, [info.lose, info.type, info.win]);

  return (
    <div className="flex flex-col gap-2 text-xs w-44">
      <div className="flex gap-1">
        <div>{title}</div>
        <div className="text-orange-400 font-bold">전체 승률</div>
        {/* 칼바람은 최근 승률 */}
      </div>
      <div className="flex flex-row gap-3 items-center">
        <img className="-m-4" src={symbol} alt="asdf" />
        <div className="flex items-center gap-1">
          <div className="font-bold text-lg">{rate}%</div>
          <div className="text-gray-400 text-xs">
            ({info.win || 0}승 {info.lose || 0}패)
          </div>
        </div>
      </div>
    </div>
  );
}

export default GameRate;
